export function calculateCRC32(data: Uint8Array) {
  const generatorPolynomial = 0x04C11DB7
  let crc = 0xFFFFFFFF
  for (let i = 0; i < data.length; i++) {
    crc ^= data[i] << 24
    for (let j = 0; j < 8; j++) {
      if (crc & 0x80000000) {
        crc = (crc << 1) ^ generatorPolynomial
      }
      else {
        crc <<= 1
      }
    }
  }
  return crc >>> 0
}
